

  //  import ImageUpload from "../../common/imageUpload.vue"
  import MultiImageUpload from "../../common/multiImageUpload.vue";
  import Voice from "../../common/voice.vue";
  import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Toast from 'vux/src/components/toast/index.vue'


  export default {
    directives: {
      TransferDom
    },
    components: {
      XTextarea,
      Group,
      XButton,
      Cell,
      Toast,
      MultiImageUpload: MultiImageUpload,
      Voice: Voice
    },
    data() {
      return {
        isGetting:false,
        toastData: {
          content: '请先完成录音再发布打卡',
          show: false
        },
        isUploading: false,
        params: {
          id: 0,
          type: ''
        },
        imageUploadData: {
          image: [], //数据格式: {"url": "xxxxx", "id": 123}
          url: this.$router.currentRoute.fullPath,
          isNeedBaseUrl: false
        },
        voiceData: {
          voice: [],  //数据格式: {"url": "xxxxx", "id": 123}
          voiceMountLimit: 1, //最多音频条数
          hideBtnAfterRecord: true, //录制完隐藏录制按钮,并且删除之前的才显示
          isRecording: null,
          isPlaying: null,
          fatherDoStop: false
        },
        viewControl: {
          textNum: 0
        },
        data: {
          content: ''
        }
      }
    },
    mounted(){
      document.getElementById("title").innerHTML = "打卡";
      var _self = this;
      this.as.extend(true, _self.params, _self.$route.query);
      console.log(this.params);
    },
    methods: {
      //阻止打卡
      preventClick(sth,event){
        var clickPos = event.offsetY;
        var maskHeight = event.target.clientHeight;
        var picAreaHeight = document.querySelector("div.multiImageUpload").clientHeight;
        if (maskHeight - picAreaHeight > clickPos) {
          this.toastData.content = "请先完成录音再输入文字";
        } else {
          this.toastData.content = "请先完成录音再添加图片";
        }
        this.toastData.show = true;
      },
      //发布打卡
      publishMoment(){
        if(this.voiceData.isRecording) {
          this.toastData.content = "请先完成录音再发布打卡";
          this.toastData.show = true;
          return
        }

//        if(this.voiceData.isPlaying) {
//          this.voiceData.fatherDoStop = true;
//        }

        var imageList = [];
        if(this.imageUploadData.image.length > 0){
          for(var i=0;i<this.imageUploadData.image.length;i++){
            var image = {
              id: this.imageUploadData.image[i].id
            };
            imageList.push(image);
          }
        }

        var audioList = [];
        if(this.voiceData.voice.length > 0) {
//          for(var i=0;i<this.voiceData.voice.length;i++){
//            var audio = {
//              id: this.voiceData.voice[i].id
//            };
//            audioList.push(audio);
//          }
          //暂时写死取最后一个音频
          audioList.push({id: this.voiceData.voice[this.voiceData.voice.length - 1].id});
        }

        //都为空时弹出提示
        if(imageList.length == 0 && this.data.content.trim() == "" && audioList.length == 0){
          this.$emit("modalAlert","请输入打卡内容","确定");
          return;
        }

        var id = this.$route.query.id;
        var type = this.$route.query.type;
        var _self = this;
        this.H.toAjax(true).then(function (data) { //data 公用参数
          var submitData = data;

          submitData.data = {
            moment: {
              content: _self.data.content,
              type: _self.params.type,
              activityId: _self.params.id,
              activityType: _self.params.type,
              imageList: imageList,
              audioList: audioList
            }
          };
          if(!_self.isGetting){
             _self.isGetting = true ;
          }else{
            _self.$emit("modalAlert", "玩命加载中～");
            return ;
          }
          _self.A.post("/v1/moment/create", submitData).then(function (result) {
            console.log(result.data.state);
            if (result.data.state.code == 0) {
              console.info("打卡返回数据");
              _self.$router.push({path:"/admin/punchShow/meet",query:{id: id,type: type}});
            } else {
              _self.$emit("modalAlert", result.data.state.msg, "确定");
              _self.isGetting = false ;
            }
          });
        });
      }
    },
    watch: {

    }
  }



